"use strict";

/**
 * Display MODE + animation
 */
document.addEventListener('DOMContentLoaded', function () {
  // console.log('Button script caricato.');
  var button_container = document.querySelector('.wlbb-btns-container') != null ? document.querySelector('.wlbb-btns-container') : '';
  if (button_container == '') return;
  var timing_show_mode = wlbb_buttons_vars.timing_show;
  var delay = wlbb_buttons_vars.delay;
  var interval = wlbb_buttons_vars.interval_switch;

  if (document.querySelector('.wlbb-btns-container.fadeIn') != null) {
    fadeIn(button_container, timing_show_mode, delay);
  } else if (document.querySelector('.wlbb-btns-container.slideUp') != null) {
    // let element = document.querySelector('.wlbb-btns-container .wlbb-height');
    slideUp(button_container, timing_show_mode, button_container.offsetHeight, delay);
  } else {
    cleanBtns(button_container);
  } // store icons variables


  if (document.querySelector(".wlbb-btns-container .wlbb-icon-pane") != null) {
    var length = document.querySelectorAll(".wlbb-btns-container .wlbb-icon-pane").length;

    if (length > 1) {
      var time_start = delay + timing_show_mode;
      setTimeout(setInterval(switchIcons, interval, length), time_start);
    }
  } // setTimeout( cleanBtns, timing_show_mode + delay, button_container );
  // cleanBtns( button_container )

});

function switchIcons(length) {
  var current = document.querySelector(".wlbb-btns-container .wlbb-icon-pane.active");
  var index = parseInt(current.dataset.count); // console.log(index);

  var next_index = index + 1 >= length ? 0 : index + 1; // console.log(next_index);

  var next = document.querySelector(".wlbb-icon-pane[data-count=".concat(CSS.escape(next_index), "]")); // fadeOut(current);

  current.classList.remove('active'); // fadeIn(next, 100);

  next.classList.add('active');
}

function fadeIn(el, time, delay) {
  setTimeout(function (el, time) {
    el.style.opacity = 0;
    var last = +new Date();

    var tick = function tick() {
      el.style.opacity = +el.style.opacity + (new Date() - last) / time;
      last = +new Date();

      if (+el.style.opacity < 1) {
        window.requestAnimationFrame && requestAnimationFrame(tick) || setTimeout(tick, 16);
      } else {
        cleanBtns(el);
      }
    };

    tick();
  }, delay, el, time);
}

function slideUp(el, time, max_height, delay) {
  setTimeout(function (el, time) {
    el.style.height = 0;
    el.style.opacity = 1;
    var last = +new Date();
    var increment = 0;

    var tick = function tick() {
      increment = increment + (new Date() - last) / (time / 100);
      el.style.height = increment + 'px';
      last = +new Date();

      if (increment < max_height) {
        window.requestAnimationFrame && requestAnimationFrame(tick) || setTimeout(tick, 16);
      } else {
        cleanBtns(el);
      }
    };

    tick();
  }, delay, el, time, max_height);
}

function cleanBtns(el) {
  el.classList.remove('overflow-hidden');
  var aButtons = document.querySelectorAll('.wlbb-btns-container .pointer-none');
  if (document.querySelector('.wlbb-height') != null) document.querySelector('.wlbb-height').remove();
  aButtons.forEach(function (aButton) {
    aButton.classList.remove('pointer-none');
  }); // console.log('Cleaned');
}

function fadeOut(el) {
  var time = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 100;
  el.style.opacity = 1;
  var last = +new Date();

  var tick = function tick() {
    el.style.opacity = el.style.opacity - (new Date() - last) / time;
    last = +new Date();

    if (+el.style.opacity > 0) {
      window.requestAnimationFrame && requestAnimationFrame(tick) || setTimeout(tick, 16);
    } else {
      cleanBtns(el);
    }
  };

  tick();
}

